[data-theme^="dark"] {
  --text:					#dcdcdc;
  --text-light:			rgba(255,255,255,.6);
  --background:			#1d1d1d;
  --editor:				rgba(0,0,0,.15);
  --main:					rgba(30,30,30,.80);
  --app-color-focus:		#343434;
  --dividers:				#484848;
  --header-border:		rgba(0,0,0,.5);
  --icons-enabled:		rgba(255,255,255,.6);
  --icons-disabled:		rgba(255,255,255,.15);
  --icons-active:			rgba(255,255,255,1);
  --toolbar-hover:		rgba(255,255,255,.06);
