*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

button.ui-button {
  padding: 3px 14px;
  color: var(--button-color);
  background: var(--button);
  border: 1px solid var(--button-border);
  background-clip: border-box;
  font-size: 13px;
  border-radius: 5px;
}

button.ui-button:focus {
  color: #fff;
  background: var(--accent);
  background-image: linear-gradient(var(--accent-light), var(--accent));
  border: 1px solid var(--accent);
  outline: none;
}

button.ui-button:active {
  color: #fff;
  background: var(--accent-dark);
  background-image: linear-gradient(var(--accent), var(--accent-dark));
  border: 1px solid var(--accent-dark);
}

button.ui-button:disabled {
  opacity: .5;
}

body {
  text-align: center;
  padding: 0;
}

body main {
  padding: 10px 20px 0;
  height: 270px;
}

body main figure {
  padding-bottom: 10px;
}

body main figure img {
  -webkit-user-drag: none;
  -webkit-app-region: no-drag;
}

body main h1 {
  font-size: 15px;
  margin-bottom: 20px;
  color: var(--text);
  font-weight: 700;
}

body main p {
  font-size: 13px;
  font-weight: 400;
  width: 160px;
  margin: 0 auto 15px;
  color: var(--text);
}

body main dl {
  font-size: 11px;
  line-height: 14px;
  margin-bottom: 15px;
}

body main dl dt, body main dl dd {
  display: inline;
  color: var(--text);
}

body main dl dt {
  padding-right: 5px;
}

body main dl dd::after {
  display: block;
  content: '';
  white-space: pre-line;
}

body main button.ui-button {
  padding: 2px 12px;
  font-size: 11px;
  border-radius: 3px;
}
